import * as React from 'react';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import Link from '../../components/Link';

export interface PrevNextButtonsProps {
  /** предыдущая страница */
  prevPage: string;
  /** активна ли кнопка Дальше */
  nextEnabled: boolean;
};

export default function PrevNextButtons(props: PrevNextButtonsProps) {
  return (
    <>
      <Grid item xs={6} sm={3}>
        <Button to={props.prevPage} fullWidth component={Link} variant="outlined" color="primary">Назад</Button>
      </Grid>
      <Grid item xs={6} sm={3}>
        <Button fullWidth disabled={!props.nextEnabled} type="submit" variant="contained" color="primary">Дальше</Button>
      </Grid>
    </>
  );
}
